.slider {
	background-image: url("../img/slider-bg.jpg");
	background-color: #1d2734;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	position: relative;
}

.slider__controls {
	position: absolute;
	bottom: 10px;
	transform: translateX(-50%);
	left: 50%;
	font-size: 0;
}

.slider__control {
	width: 13px;
	height: 7px;
	background: #e8e8e8;
	margin-right: 10px;
	display: inline-block;
	vertical-align: top;
	border-radius: 1px;
}

.slider__control:last-child {
	margin-right: 0;
}

.slider__control--active {
	background: linear-gradient(#feab16, #ff7500) #ff9000;
}

.slider__title {
	font-family: "Panton", Arial, sans-serif;
	font-size: 30px;
	line-height: 15px;
	color: #e7e7e7;
	font-weight: normal;
	width: 732px;
	margin: 0;
	margin: 35px 0 25px;
}

.slider__description {
	color: #b0b0b0;
	font-size: 20px;
	line-height: 25px;
	width: 732px;
	margin: 0;
}

.slide {
	padding: 70px 0 85px;
	position: relative;
}

.slide:after {
	content: "";
	display: block;
	position: absolute;
	bottom: 0;
	right: 0;
}

.slide--1:after {
	background: url("../img/slider-img-1.png") no-repeat;
	width: 310px;
	height: 399px;
}

.slide--2 {
	display: none;
}

.slide--2:after {
	background: url("../img/slider-img-2.png") no-repeat;
	width: 279px;
	height: 409px;
}

.slide--3 {
	display: none;
}

.slide--3:after {
	background: url("../img/slider-img-3.png") no-repeat;
	width: 332px;
	height: 418px;
}

